@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
 
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  border: none;
  outline: none;
  scroll-behavior: smooth;
  font-family: "Poppins", sans-serif;
}
 
:root {
  --bg-color: rgb(245,245,245);
  --second-bg-color: rgb(251, 251, 251);
  --text-color: #242423;
  --second-text-color: rgb(120,120,120);

  /*Color Options*/
  /* --main-color: rgb(30, 200, 90); */
  --main-color: rgb(30, 100, 50);

  --breakpoint-one: 991px;
}
 
*::selection {
  background: var(--main-color);
  color: var(--bg-color);
}
 
html {
  font-size: 62.5%;
  overflow-x: hidden;
}
 
body {
  background: var(--bg-color);
  color: var(--text-color);
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 991px) {
  .header {
    padding: 2rem 3%;
  }

  .footer {
    padding: 2rem;
  }
}