.contact{
    background-color: var(--second-bg-color);

    padding: 5rem 15%;

    min-height: calc(30vh - 8rem);

    overflow: auto;
    overflow-x: hidden;
}

.contact-text h2{
    line-height: 1.2;
    font-size: 5rem;
}

.contact-text h4{
    font-size: 2.5rem;
    color: var(--main-color);
}

.contact-box-container{
    display: flex;
    align-items: center;
}

.contact-item-container{
    display: flex;
    align-items: center;
    /* justify-content: center; */

    margin-top: 3vw;
    margin-right: 5vw;
    color: var(--text-color);

    height: 4vw;
    width: 100vw;
}

.contact-container-icon{
    width: 6rem;
    height: 6rem;
    border-radius: 50%;


    background-color: var(--second-bg-color);
    box-shadow: 0 0 0.5vw 0 rgb(200,200,200);
}

.contact-icon{
    font-size: 4rem;
    text-align: center;
    margin-top: .4rem;
    color: var(--main-color);
}

.contact-item-text{
    font-size: 2rem;
    padding-left: 2rem;
    color: var(--second-text-color);
}

.contact-item-text h5{
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--main-color);
}

.contact-item-text span{
    font-size: 2rem;
    font-weight: 400;
}

.contact-item-text a{
    font-size: 2rem;
    font-weight: 400;
    transition: .2s;
    color: var(--second-text-color);
}

.contact-item-text a:hover{
    color: var(--main-color)
}

@media (max-width: 901px){
    .contact{
        display: block;
        flex-flow: column nowrap;
    }

    .contact-text{
        order: 1;
        text-align: center;
    }

    .contact-box-container{
        flex-direction: column;
    }
    
    .contact-item-container{
        padding: 3rem 5% 3rem;
    }

    .contact-item-container:nth-child(1){
        order: 2;
        display: flex;
        justify-content: center;
    }

    .contact-item-container:nth-child(2){
        order: 3;
        display: flex;
        justify-content: center;
    }
}