.header{
    background-color: var(--bg-color);

    box-sizing: border-box;

    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 9%;
    width: 100%;
    z-index: 100;
    height: 7.5vh;
    min-height: 7.5vh;

    border-bottom: .1vw solid var(--bg-color);
    box-shadow: 0 0 1rem rgb(200, 200, 200);
}

.homepage-logo{
    font-size: 2.5rem;
    font-weight: 600;
    text-decoration: none;
    color: var(--main-color);
    cursor: default;
}

.homepage-logo:hover{
    color: var(--text-color);
}

.some-class{
    background-color: red;
}

nav a{
    font-size: 1.7rem;
    color: var(--text-color);
    text-decoration: none;
    font-weight: 500;
    margin-left: 4rem;
    transition: .3s ease;
    
}

nav a:hover,
nav a.active{
    color: var(--main-color);
}

#menu-icon{
    font-size: 3.6rem;
    color: var(--text-color);
    display: none;
}

@media screen and (max-width: 991px) {
    .header {
      padding: 2rem 3%;
    }
}

@media screen and (max-width: 768px) {
    #menu-icon {
        display: block;
        cursor: pointer;
    }

    nav{
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        padding: 1rem 3%;
        background-color: var(--bg-color);
        border-top: .1rem solid rgba(0,0,0,.2);
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
    }

    nav a{
        display: block;
        font-size: 2rem;
        margin: 3rem 0;
    }
}