.about {
    background-color: var(--second-bg-color);

    min-height: 75vh;
    padding: 2.5vw 15% 2.5vw 15%;

    display: flex;
    align-items: center;
    justify-content: center;

    overflow: auto;
    overflow-x: hidden;

    box-sizing: border-box;
}

.about-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-images{
    position: relative;
    top: 0;
    left: 0;
}

.computer-image{
    position: relative;
    top: 0;
    left: 0;
    width: 40vw;
    z-index: 0;
    box-shadow: 0 0 .75vw var(--main-color);
}

.emoji-container{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -3vw;
    right: -3vw;

    width: 12vw;
    aspect-ratio: 1;

    border-radius: 50%;
    background-color:var(--second-bg-color);
}

.ham-emoji-image{
    width: 7vw;
    animation: ham-rotate 4s infinite linear;   
}

@keyframes ham-rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.about-content{ 
    width: 25vw;
    padding-left: 5vw;
    text-align: left;
    /* margin-left: 5vw; */
}

#about-heading-me{
    color: var(--main-color);
}

.about-content-heading{
    width: 25vw;
}

.about-content-heading h2{
    line-height: 1.2;
    font-size: 5rem;
}

.about-content-subheading h3{
    font-size: 2.6rem;
    width: 35vw;
}

.about-content-body p{
    font-size: 1.6rem;
    margin: 2rem 0 0;
    width: 20vw;
}

@media (max-width: 991px) {
    .about{
        padding: 5rem 3% 2rem;
        display: flex;
        justify-content: space-evenly;
    }

    .about-container{
        display: flex;
        flex-flow: column;
    }

    .about-images{
        order: 1;
        width: 50vw;
        display: flex;
        transform: scale(1.2);
    }

    .computer-image{
        width: 50vw;
    }

    .emoji-container{
        width: 20vw;
    }

    .ham-emoji-image{
        width: 11.66vw;
    }

    .about-content{
        padding-top: 7.5vw;
        padding-left: 0vw;
        order: 2;
        width: 90vw;
        text-align: center;

        display: block;
        justify-content: center;
    }

    .about-content-heading {
        display: flex;
        justify-content: center;
        margin: auto;
        width: 50vw;
    }

    .about-content-subheading{
        margin-bottom: 0rem;
        display: flex;
        justify-content: center;
    }

    .about-content-subheading h3{
        width: 50vw;
    }

    .about-content-body{
        display: flex;
        justify-content: center;
    }

    .about-content-body p{
        width: 50vw;
    }
}