.portfolio {
    background-color: var(--bg-color);

    min-height: 100vh;
    padding: 5rem 15% 5rem;

    overflow: auto;
}

.portfolio-container-heading{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vw 0 4vw 0;
}

.portfolio-heading{
    font-size: 6rem;
}

.portfolio-heading span{
    color: var(--main-color);
}

.portfolio-container-projects{
    display: grid;
    grid-template-columns: repeat(1fr, 1);
    align-items: center;
    justify-content: center;
    gap: 2vw;
    padding-bottom: 5vw;

}

.portfolio-box{
    position: relative;
    border-radius: 2vw;

    width: 60vw;
    height: auto;

    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 2vw 0% 2vw 0;

    background-color: var(--second-bg-color);

    box-shadow: 0 0 .5vw 0 rgb(200, 200, 200);
}

.portfolio-box-image-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30vw;
    border-radius: 2vw;

    overflow: hidden;
    object-fit: cover;

    box-shadow: 0 0 .5vw 0 rgb(200, 200, 200);

    transition: .5s ease;
}

.portfolio-box-image-container:hover{
    box-shadow: 0 0 1vw var(--main-color);
}

.stock-img{
    max-width: 100%;
    transition: .75s ease-in;

    display: flex;
    align-items: center;
}

.stock-img:hover{
    transform: scale(1.05);
    transform-origin: center;
}

.portfolio-content-text {
    display: block;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.portfolio-content h4{
    padding: 0vw 0vw 0vw 0vw;
    font-size: 3rem;
}

.portfolio-content p{
    width: 20vw;
    margin-top: 1vw;
    font-size: 1.5rem;

    color: var(--text-color);
    font-weight: 200;
    text-align: center;
}

.portfolio-content-links{
    display: flex;
    align-items: center;
    justify-content: center;

    padding-top: 2.5vh;
    padding-bottom: 2.5vh;

    font-size: 2rem;

}

.portfolio-content-links span{
    font-size: 1.5rem;
    padding: .5vw;
}

.github-container{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;

    color: var(--main-color);
    transition: 0.2s;
}

.github-container:hover{
    color: var(--text-color);
}

.text-github{
    font-size: 2rem;
    margin-right: .5vw;
    font-weight: 600;
}

.icon-github{
    display: flex;
    align-items: center;
    font-size: 3.5rem;
}

@media screen and (max-width: 991px){
    .portfolio{
        display: flex;
        flex-flow: column;
    }

    .portfolio-heading{
        order: 1;
        text-align: center;
    }

    .portfolio-box:nth-child(1){
        order: 2;
    }

    .portfolio-box:nth-child(2){
        order: 3;
    }

    .portfolio-box:nth-child(3){
        order: 4;
    }

    .portfolio-box{
        display: flex;
        flex-flow: column;
    }

    .portfolio-box-image-container{
        order: 1;
    }

    .portfolio-content{
        order: 2;
    }

    .portfolio-box{
        width: auto+10vw;
        height: auto;
        padding: 2vw 2vw 2vw 2vw;
    }

    .portfolio-box-image-container{
        width: auto;
        height: 35vw;
    }

    .portfolio-content-text{
        display: block;
        justify-content: center;
        margin: auto;
    }

    .portfolio-content-text h4{
        padding: 3vw 0vw 2vw 0vw;
    }

    .portfolio-content-text-body p{
        display: block;
        justify-content: center;
        margin: auto;
        width: 40vw;
    }
}