.footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 2rem 9%;

    background-color: rgb(60,60,60);

    box-shadow: 0 0 1rem rgb(200, 200, 200);

    min-height: 8rem;
}

.footer-text p{
    color: white;
    font-size: 2rem;
    font-weight: 700;
}

.footer-icons a{
    color: white;
    font-size: 3.5rem;
    padding-left: 2vw;

    display: inline-block;
    align-items: center;
    justify-content: center;

    transition: .2s;
}

.footer-icons a:hover{
    color: var(--main-color);
}

@media (max-width: 991px) {
   .footer {
    padding: 2rem;
  }
}

@media (max-width:648px){
    .footer{
        display: flex;
        flex-flow: column;
    }

    .footer-text{
        order:1
    }

    .footer-icons{
        order:2;
        margin-top: 1vh;
    }
}